@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@300;400;600&display=swap');

@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@200;400;700&family=Roboto+Mono:wght@300;400;600&display=swap');
body {
	background-color: #f4f4f9;
	font-family: 'Montserrat', sans-serif;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
}

.App {
	width: 100%;
}
.app-container {
	width: 100%;
	height: calc(100vh - 20px);
	display: flex;
	/* flex-wrap: wrap; */
	gap: 50px;
	padding: 3%;
	justify-content: space-between;
}
.header {
	display: flex;
	align-items: center;
	justify-content: center;
	height: 50px;
	width: 100%;
	background-color: #d9d9d9;
}
.header-img {
	width: 40px;
	height: 40px;
}
.image {
	width: 300px;
}

.side-panel {
	display: flex;
	flex-direction: column;
	gap: 150px;
}
.content {
	display: flex;
	flex-direction: column;
	width: 500px;
	height: 300px;
	background-color: rgb(255, 255, 255);
	/* margin-top: 30px; */
	border-radius: 10px;
	padding: 0 60px;
}
.content__title {
	margin-top: 30px;
	font-size: 22px;
	font-weight: bold;
}
.content__prise {
	margin-top: 10px;
	color: #ff0000;
	font-weight: bold;
	font-size: 32px;
}
.last_prise {
	font-size: 20px;
	text-decoration: line-through;
	font-weight: normal;
	color: #5e5e5e;
}
.content_button {
	margin-top: 60px;
	width: 90%;
	/* height: 50px; */
	text-align: center;
	padding: 25px 0;
	background-color: #413c3c;
	align-self: center;
	color: #fff;
	border-radius: 3px;
	font-size: 24px;
	font-weight: 500;
	font-family: 'Montserrat', sans-serif;
	transition: all 1s ease;
	margin-bottom: 20px;
}
.content_button:hover {
	background-color: #000000;
	border-radius: 15px;
}
.photo-slider-box {
	background-color: rgb(255, 255, 255);
	max-width: 1100px;
	min-width: 250px;
	/* margin: 30px 3%; */
	padding: 1% 1%;
	border-radius: 10px;
	display: flex;
	align-items: center;
	justify-content: center;
}
.photo-slider {
}
.description {
	padding: 20px;
	font-size: 18px;
	border-radius: 10px;
	width: 500px;
	font-weight: 500;
	line-height: 22px;
	max-height: 1000px;
	background-color: rgb(255, 255, 255);
}

@media screen and (max-width: 1520px) {
	.photo-slider-box {
		max-width: 1000px;
	}
	.content {
		width: 350px;
		padding: 0 30px;
	}
	.description {
		width: 350px;
		font-size: 14px;
		line-height: 18px;
	}
	.content__title {
		margin-top: 20px;
		font-size: 18px;
	}
	.content_button {
		font-size: 18px;
		margin-top: 30px;
		margin-bottom: 20px;
	}
}
@media screen and (max-width: 1150px) {
	.app-container {
		flex-wrap: wrap;
		gap: 20px;
	}
	.side-panel {
		flex-direction: row;
		flex-wrap: wrap;
		gap: 20px;
	}
}

.form-box {
	padding-top: 50%;
}
